.main {
    display: flex;
    /*margin: 0 25% 0 25%;*/
}

.mainLeft {
    flex-direction: row;
    margin-left: 20%;
}

.mainRight {
    flex-direction: row-reverse;
    margin-right: 20%;

}

.avatar {
    display: flex;
    align-items: flex-end;
}

.avatarLeft {
    flex-direction: row;
    margin: 20px 0px 20px 20px;
}

.avatarRight {
    flex-direction: row-reverse;
    margin: 20px 20px 20px 0px;
}

.avatar img {
    height: 100px;
    width: 100px;
    border-radius: 50px;
}

.angle {
    height: 30px;
    width: 30px;
    background-color: white;
}

.in {
    height: 30px;
    width: 30px;
    background-color: #d0eca1;
}

.inLeft {
    border-radius: 0 0 30px 0;
}

.inRight {
    border-radius: 0 0 0 30px;
}

.text {
    display: flex;
    background-color: white;
    padding: 15px 15px 10px 20px;
    flex-wrap: wrap;
    min-width: 25%;
    max-width: 35%;
    justify-content: flex-start;
    flex-direction: column;
}

.textLeft {
    border-radius: 30px 30px 30px 0;
    margin: 20px 20px 20px 0px;
}

.textRight {
    border-radius: 30px 30px 0 30px;
    margin: 20px 0 20px 20px;
}

.name {
    font-weight: bold;
    color: #033803;
    line-height: 30px;
}

.message {
    line-height: 30px;
    width: 90%;
}

.time {
    text-align: right;
    color: #727171;
    font-size: 16px;
    margin: 0 10px 10px 0;
}
